@import url(https://fonts.googleapis.com/css2?family=Roboto&family=Russo+One&display=swap);
html,
body,
#root,
#root > div,
div[data-reactroot] {
  height: 100%;
  padding: 0;
  margin: 0;
}

.homeBackground {
  background-color: #212a50;
  position: relative;
  background-position: center;
  width: 100vw;
  height: calc(100vh - 56px);
  object-fit: cover;
}

.backgroundVideo {
  transform: translate(-50%, -50%);
  position: absolute;
  top: 50%;
  left: 50%;
  width: 50vw;
  height: 50vh;
}

.iconSkills {
  width: 50px;
  height: 50px;
  margin-right: 15px;
}

.aboutMe {
  padding: 25px;
  color: white;
  font-family: "Russo One", sans-serif;
}

/* Place a background picture to the about me page  */
.aboutMeBackground {
  background-image: url(/static/media/DSC3399.79eaacb9.jpg);
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  overflow-y: scroll;
  height: calc(100vh - 56px);
  /* Hide scrollbar for IE, Edge and Firefox */
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

/* Hide scrollbar for Chrome, Safari and Opera */
.aboutMeBackground::-webkit-scrollbar {
  display: none;
}

/* Change properties for the div tag with this class */
.pAboutMe {
  font-family: "Roboto", sans-serif;
  color: white;
  background-color: #4664866b;
  position: relative;
  text-align: justify;
  padding: 35px;
}

/* Center the thumbnail image to the column, more noticeable in small screens */
.img-thumbnail {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10px;
  height: 200px;
  width: 200px;
}

/* Centralize the button */
.divBtn {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Add the properties to the image credit text  */
#imageCredit {
  font-family: "Didact Gothic", sans-serif;
  font-weight: bolder;
  color: white;
}

.portfolio {
  padding: 25px;
}

/* Place a background picture to the portfolio page  */
.portfolioBackground {
  background-image: url(/static/media/markus-spiske-Fa0pTKuoDVY-unsplash.eba45b07.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  overflow-y: scroll;
  width: 100vw;
  height: calc(100vh - 56px);
}
/* Add the properties to the image credit text  */
#imageCredit {
  font-family: "Didact Gothic", sans-serif;
  color: rgba(255, 255, 255, 0.507);
  position: fixed;
  bottom: 30px;
}

/* Place a background picture to the about me page  */
.contactMeBackground {
  background-image: url(/static/media/DSC4576.6cf1dc1c.jpg);
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  overflow-y: scroll;
  height: calc(100vh - 56px);
  /* Hide scrollbar for IE, Edge and Firefox */
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

.contacMe {
  padding: 25px;
}
/* Hide scrollbar for Chrome, Safari and Opera */
.contactMeBackground::-webkit-scrollbar {
  display: none;
}

/* Change properties for the div tag with this class */
.pContacMe {
  font-family: "Russo One", sans-serif;
  letter-spacing: 10px;
  color: white;
  background-color: #4664866b;
  position: absolute;
  text-align: center;
  padding: 50px;
}

.flip-card {
  background-color: transparent;
  width: 300px;
  height: 300px;
  perspective: 1000px;
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
}

.flip-card-front, .flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.flip-card-front {
  background-color: #46648696;
  color: black;
}

.flip-card-back {
  background-color: #466486;
  color: white;
  transform: rotateY(180deg);
}
/* Set font color and background color to the cards buttons */
.btnPortfolio {
  background-color: #46648685;
  color: whitesmoke;
  font-family: "Russo One", sans-serif;
  text-decoration: none;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0px 5px 10px 5px;
  padding: 5px;
  border-radius: 8px;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
/* Change button color when mouse hover the button */
.btnPortfolio:hover {
  background-color: #797b7c85;
}

.card-img {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* Limit the card text height */
.card-text {
  font-family: "Roboto", sans-serif;
  height: 240px;
  overflow-y: auto;
  padding-right: 5px;
  text-align: justify;
}

.card-title {
  font-family: "Russo One", sans-serif;
  color: rgba(255, 255, 255, 0.801);
  margin-top: 5px;
  height: 20px;
  text-align: center;
}

